import { UserType } from '../HW8'

type ActionType =
    | { type: 'sort'; payload: 'up' | 'down' }
    | { type: 'check'; payload: number }

export const homeWorkReducer = (state: UserType[], action: ActionType): UserType[] => {
    switch (action.type) {
        case 'sort': {
            const newState = [...state]  // Создаем новый массив для сохранения неизменности
            return newState.sort((a, b) => {
                if (action.payload === 'up') {
                    return a.name > b.name ? 1 : -1
                } else {
                    return a.name < b.name ? 1 : -1
                }
            })
        }
        case 'check': {
            const newState = state.filter(user => user.age >= action.payload) // Фильтрация по возрасту
            return newState
        }
        default:
            return state
    }
}